























import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class PopupWin extends Vue {
  noteContent: string = '';

  get inputNum() {
    return this.noteContent.length;
  }

  closeRemarkTag() {
    this.$emit('closePopupWin', false);
  }

  OK() {
    this.$store.commit('RecordStore/setNote', this.noteContent);
    this.$emit('closePopupWin', false);
  }
};
